import React from 'react'
import '../css/style.css'
import HomeIcon from '@material-ui/icons/Home';
import $ from  'jquery'


var background__height = 0;
var background__minus__headerHeight = 0;
var windowHeight = 0;
var windowWidth = 0;
$(document).ready(function(){
    windowHeight = $(window).height();
    windowWidth = $(window).width();

    console.log("current windowHeight =", windowHeight);
    console.log("current windowWidth =", windowWidth);



    background__height = $(window).height();
    background__minus__headerHeight = background__height - 350;
    console.log("image size is:", background__minus__headerHeight);
   $("#image").css('height', background__minus__headerHeight);
})

function Home() {
    return (
        <div className="home">
            <nav>
                <div className="first__layer__nav">
                    <div className="first__layer__nav__flex">
                        <div className="first__layer__nav__flex__1">
                            <div className="verticalAlign text-wrap h1-hover">
                                <h1 id="active">Privatkunde</h1>
                            </div>
                        </div>
                        <div className="first__layer__nav__flex__2">
                            <div className="verticalAlign h1-hover text-wrap">
                                <h1>Firmenkunde</h1>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="secound__layer__nav">
                    <div className="secound__layer__nav__flex">
                        <div className="secound__layer__nav__flex1">
                            <div className="secound__layer__nav__flex__icon">
                                <div className="secound__layer__nav__flex__icon1">
                                    <svg className="MoneyIcon" focusable="false" viewBox="0 0 24 24" aria-hidden="true"><path d="M11.8 10.9c-2.27-.59-3-1.2-3-2.15 0-1.09 1.01-1.85 2.7-1.85 1.78 0 2.44.85 2.5 2.1h2.21c-.07-1.72-1.12-3.3-3.21-3.81V3h-3v2.16c-1.94.42-3.5 1.68-3.5 3.61 0 2.31 1.91 3.46 4.7 4.13 2.5.6 3 1.48 3 2.41 0 .69-.49 1.79-2.7 1.79-2.06 0-2.87-.92-2.98-2.1h-2.2c.12 2.19 1.76 3.42 3.68 3.83V21h3v-2.15c1.95-.37 3.5-1.5 3.5-3.55 0-2.84-2.43-3.81-4.7-4.4z"></path></svg>
                                </div>
                                <div className="secound__layer__nav__flex__icon2">
                                    <h2>Master Bank</h2>
                                </div>
                                <h2 className="absolute GermanyPosition">Germany</h2>
                            </div>
                        </div>
                        <div className="secound__layer__nav__flex2">
                            <div className="secound__layer__nav__flex2__online__banking__flex">
                                <div className="secound__layer__nav__flex2__online__banking__flex1">
                                    <h2 className="online_Banking__h2__style">Online-Banking</h2>
                                </div>
                                <div className="secound__layer__nav__flex2__online__banking__flex2">
                                <div className="select-wrapper">
                                    <div className="secound__layer__nav__flex2__online__banking__flex2__select__wrapper__flex">
                                        <div className="second__layer__nav__flex2__online__banking__flex2__select__wrapper__flex1">
                                            <select>
                                                <option>Privat</option>
                                                <option>Firma</option>
                                            </select>
                                        </div>
                                        <div className="second__layer__nav__flex2__online__banking__flex2__select__wrapper__flex2">
                                            <input type="text" placeholder="Name" />
                                        </div>
                                        <div className="second__layer__nav__flex2__online__banking__flex2__select__wrapper__flex3">
                                            <input type="password" placeholder="Pin" />
                                        </div>
                                        <div className="second__layer__nav__flex2__online__banking__flex2__select__wrapper__flex4">
                                            <button>Login</button>
                                        </div>
                                    </div>
                                </div>
                                </div>
                            </div>
                        </div>
                        <div className="secound__layer__nav__flex3">
                            
                        </div>
                    </div>
                </div>
            </nav>
            <main>
                <div className="image__header__nav__bar">
                    <div className="image__header__nav__bar__position">
                        <div className="image__header__nav__bar__position__flex">
                            <div className="image__header__nav__bar__position__flex1">
                                <div className="home__icon">
                                    <svg class="home__icon__icon" focusable="false" viewBox="0 0 24 24" aria-hidden="true"><path d="M10 20v-6h4v6h5v-8h3L12 3 2 12h3v8z"></path></svg>
                                </div>
                            </div>
                            <div className="image__header__nav__bar__position__flex2">
                                <div className="home__text">
                                    <h4>Products</h4>
                                </div>
                            </div>
                            <div className="image__header__nav__bar__position__flex3">
                                <div className="home__text">
                                    <h4>PrivateBanking</h4>
                                </div>
                            </div>
                            <div className="image__header__nav__bar__position__flex4">
                                <div className="home__text">
                                    <h4>Investment</h4>
                                </div>
                            </div>
                            <div className="image__header__nav__bar__position__flex5">
                                <div className="home__text">
                                    <h4>Service</h4>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div id="image" className="background_image"></div>
                <div className="flying__container">
                    <div className="white__container">
                        <div className="text__container">
                            <h3 className="white__container__text__container__content">Gönn dir entspannung</h3>
                            <h2 className="flying__container__white__container__text__container__h2">Digitale übersicht von überall</h2>
                            <p className="flying__container__white__container__text__container__p">Mit einem Kredit der MaskterBank können sie ihre träume erfüllen und ihren geliebten eine Freude bereiten</p>
                        </div>
                        <div className="button__container">
                            <div className="button__position">
                                <button className="button__container__button">mehr erfahren <svg class="arrow_right" focusable="false" viewBox="0 0 24 24" aria-hidden="true"><path d="M8.59 16.59L13.17 12 8.59 7.41 10 6l6 6-6 6-1.41-1.41z"></path></svg></button>
                            </div>
                        </div>
                    </div>
                </div>
            </main>
        </div>
    )
}

export default Home
